<template>
    <div class="search-wrapper">
        <div class="search-input">
            <input type="text" v-model="keyword" @keyup="get($event)" @keydown.down="selectDown" @keydown.up.prevent="selectUp" @keyup.enter="search">
            <span class="search-reset" @click="clearInput()" v-show="keyword">&times;</span>
            <button class="search-btn" @click="search">搜索</button>
            <div class="search-select">
                <transition-group name="itemfade" tag="ul" mode="out-in" v-cloak>
                    <li v-for="(value,index) in myData" :class="{selectback:index==now}" class="search-select-option search-select-list" @mouseover="selectHover(index)" @click="selectClick(index)" :key="value">
                        {{value}}
                    </li>
                </transition-group>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    data(){
        return{
            myData:[],
            keyword:'',
            url:'https://sug.so.360.cn/suggest',
            now:-1,
            searchSrc:'https://www.baidu.com/s?ie=utf-8&f=8&rsv_bp=0&rsv_idx=1&tn=baidu&wd='
        }
    },
    methods:{
        get(ev){
            // 如果按键是上或者下，return
            if(ev.keyCode == 38 || ev.keyCode == 40){
                return;
            }
            let param = {
                word:this.keyword,
                encodein:'utf-8',
                encodeout:'utf-8'
            }
            this.$jsonp(this.url,param).then((res)=>{
                this.myData = res.s;
            })
            this.now = -1;
        },
        selectHover(index){
            this.now = index;
        },
        selectClick(){
            this.keyword = this.myData[index];
            this.search();
        },
        clearInput(){
            this.keyword = '';
            let param = {
                word:'',
                encodein:'utf-8',
                encodeout:'utf-8'
            }
            this.$jsonp(this.url,param).then((res)=>{
                this.myData = res.s;
            })
        },
        selectDown(){
            this.now++;
            if(this.now == this.myData.length){
                this.now = 0;
            }
            this.keyword = this.myData[this.now];
        },
        selectUp(){
            this.now--;
            if(this.now == -1){
                this.now = this.myData.length - 1;
            }
            this.keyword = this.myData[this.now];
        },
        search(){
            window.open(this.searchSrc+this.keyword);
        }
    }
}
</script>

<style lang="scss" scoped>
$img-path-to-assets : './../assets/image';
.search-wrapper{
    width: 100%;
    height: 100%;
    background: url(#{$img-path-to-assets}/background.jpg) no-repeat;
    background-size: 100% 100%;
    overflow: hidden;
    .search-input{
        height: 45px;
        width: 600px;
        margin: 0 auto;
        margin-top: 15%;
        position: relative;
        input{
            border: 1px solid #e4e4e4;
            box-sizing: border-box;
            width: 500px;
            height: 45px;
            font-size: 18px;
            float: left;
            padding-left: 10px;
            padding-right: 10px;
            overflow: hidden;
        }
        .search-btn{
            height: 45px;
            width: 100px;
            border: 1px solid mediumseagreen;
            background-color: mediumseagreen;
            color: white;
            font-size: 16px;
            font-weight: bold;
            float: left;
            cursor: pointer;
        }
        .search-reset{
            width: 21px;
            height: 21px;
            position: absolute;
            display: block;
            line-height: 21px;
            text-align: center;
            cursor: pointer;
            font-size: 20px;
            right: 110px;
            top: 12px
        }
        .search-select {
            position: absolute;
            top: 45px;
            width: 500px;
            box-sizing: border-box;
            z-index: 999;
            li{
                border: 1px solid #d4d4d4;
                border-top: none;
                border-bottom: none;
                background-color: #fff;
                width: 100%;
            }
            .search-select-option {
                box-sizing: border-box;
                padding: 7px 10px;
            }

            .selectback {
                background-color: #eee !important;
                cursor: pointer
            }
        }
    }
}
.search-select-list {
    transition: all 0.5s
}

.itemfade-enter,
.itemfade-leave-active {
    opacity: 0;
}

.itemfade-leave-active {
    position: absolute;
}

.selectback {
    background-color: #eee !important;
    cursor: pointer
}
.search-select ul{margin:0;text-align: left; }
</style>



